.basket-page {
  padding: 40px;
}

.basket__main-title {
  padding: 0;
}

.basket__desc {
  text-transform: uppercase;
  border-bottom: 5px solid #888f98;
  margin-bottom: 50px;
  padding-bottom: 5px;
  font-size: 22px;
}

.basket__rules {
  border: 1px solid #2a71fd;
  border-radius: 30px;
  flex-direction: column;
  align-self: center;
  gap: 20px;
  padding: 20px;
  font-size: 20px;
  display: flex;
}

.basket__rule {
  margin-left: 20px;
  font-size: 16px;
}

.basket__link {
  color: #2a71fd;
  font-size: 18px;
  font-weight: 700;
}

.basket__link:hover {
  text-shadow: 1.5px 1.5px 2px #835486;
}

.basket__list-basket {
  flex-direction: column;
  gap: 20px;
  margin: 0;
  padding: 0;
  font-size: 52px;
  font-weight: 700;
  list-style-type: none;
  display: flex;
}

.basket__item-basket {
  cursor: pointer;
  border: 2px solid #888f98;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 10px;
  font-size: 18px;
  font-weight: 300;
  transition: all .3s;
  display: flex;
}

.basket__item-basket:hover {
  border-radius: 20px;
  transition: all .3s;
  box-shadow: 0 0 5px 5px #010101e6;
}

.basket__title-product {
  width: 50%;
}

.basket__title-product:hover {
  color: #2a71fd;
}

.basket__img-product {
  border: 1px solid #888f98;
  border-radius: 10px;
  width: 10%;
  min-width: 120px;
  height: 100px;
  transition: all .1s;
}

.basket__img-product:hover {
  border: 5px solid #2a71fd;
  transition: all .1s;
}

.basket__price-product {
  width: 15%;
}

.basket__btn-order {
  color: #2a71fd;
  border: 1px solid #2a71fd;
  border-radius: 5px;
  width: 270px;
  padding: 25px 15px;
  font-size: 24px;
  transition: background, color .5s;
  display: block;
}

.basket__btn-order:hover {
  color: #fff;
  background-color: #2a71fd;
  transition: background, color .5s;
  box-shadow: 0 0 5px 5px #010101e6;
}

@media (width <= 1000px) {
  .basket__main-title {
    margin: 0;
    padding: 0 !important;
  }

  .basket__desc {
    text-transform: none;
    margin: 0 0 20px;
    font-size: 18px;
  }

  .basket__item-basket {
    flex-direction: column;
    display: flex;
  }

  .basket__img-product {
    width: 70%;
    height: 120px;
  }

  .basket__title-product {
    text-align: center;
    width: 100%;
    font-size: 18px;
    font-weight: 700;
    margin: 0 !important;
    padding: 0 !important;
  }

  .basket__price-product {
    text-align: center;
    width: 100%;
    font-size: 22px;
    font-weight: 700;
    margin: 0 0 5px !important;
    padding: 0 !important;
  }

  .basket__btn-order {
    width: 100%;
  }
}

